body {
  /* This defines what 1rem is */
  font-size: 62.5% !important; /*1rem = 10px, 10px / 16px = 62.5% */

  background-image: linear-gradient(
      to right,
      rgba(224, 78, 59, 0.7),
      rgba(204, 255, 230, 0.7)
    ),
    url(../src/img/samuel-zeller-154708-unsplash.jpg);

  background-size: cover;
}

.header {
  color: aliceblue !important;
  font-size: 0.8rem;
}

@media (max-width: 1200px) {
  font-size: 56.25% !important; /* 1rem = 9px, 9px / 16px = 56.25% */
}

@media (max-width: 900px) {
  font-size: 50% !important; /* 1rem = 8px, 8px / 16px = 50% */
}

@media (max-width: 600px) {
  font-size: 43.75% !important; /* 1rem = 7px, 7px / 16px = 43.75% */
}

@media (min-width: 1800px) {
  font-size: 75% !important; /* 1 rem = 12px, 12px /16px = 75% */
}

.item:hover {
  -webkit-transform: scale(1.1);
  -moz-transform: scale(1.1);
  -o-transform: scale(1.1);
  transform: scale(1.1);
}

.segment {
  opacity: 0.7 !important;
}

.segment > h4 {
  color: black !important;
}
