.search-container {
  display: flex !important;
  align-items: center !important;
  justify-content: center !important;
}

.search-bar:hover {
  -webkit-box-shadow: rgba(0, 0, 0, 0.8) 0px 5px 15px,
    inset rgba(0, 0, 0, 0.15) 0px -10px 20px;
  -moz-box-shadow: rgba(0, 0, 0, 0.8) 0px 5px 15px,
    inset rgba(0, 0, 0, 0.15) 0px -10px 20px;
  -o-box-shadow: rgba(0, 0, 0, 0.8) 0px 5px 15px,
    inset rgba(0, 0, 0, 0.15) 0px -10px 20px;
  box-shadow: rgba(0, 0, 0, 0.8) 0px 5px 15px,
    inset rgba(0, 0, 0, 0.15) 0px -10px 20px;
}

.input-container {
  display: flex !important;
  justify-content: center !important;
  flex-wrap: wrap !important;
}

.search-bar {
  background-color: rgba(209, 154, 123, 0.7) !important;
  padding: 14px 30px !important;
  margin: 20px 0 !important;
  text-align: center;
  width: 80%;
}

.field > label {
  font-size: 2.2rem !important;
  color: antiquewhite !important;
  padding-bottom: 10px;
  font-family: 'Ultra', serif;
  letter-spacing: 0.2rem;
}

@media (max-width: 600px) {
  .field > label {
    font-size: 1.5rem !important;
  }
}

.field {
  font-size: 1rem;
  padding: 2px 40px;
}

.button {
  margin-top: 12px !important;
  cursor: pointer;
}

.button:hover {
  transform: translateY(-2px);
  box-shadow: 0 0.2rem 0.4rem rgba(0, 0, 26, 0.8) !important;
}

.button:active {
  transform: translateY(-1px);
  box-shadow: 0 0.1rem 0.2rem rgba(0, 0, 26, 0.8) !important;
}
